<template>
	<div class="container">
		<div class="row min-vh-100 align-items-center justify-content-center">
			<div class="col-md-5">
				<div class="card">
					<div class="card-header bg-white text-center">
						<img :src="this.laravel.assets + 'img/brands/silta-color.svg'" class="img-fluid" />
					</div>
					<div class="card-body">
						<h3 class="text-center">Mengarahkan</h3>
						<p class="text-muted text-center m-0">Tunggu sebentar, kami akan mengarahkan anda!</p>
					</div>
				</div>
				<Footer />
			</div>
		</div>
	</div>
</template>

<style lang="scss">
.svg-inline--fa.fa-w-18 {
	width: 1.25rem;
}
</style>

<script>
import Footer from './templates/Footer.vue';
import Router from '../router.js';

export default {
	components: {
		Footer
	},
	data() {
		return {
			laravel: window.laravel,
		}
	},
	mounted: function() {
		document.title = 'Mengarahkan...';


		$.post(window.laravel.base_url + '/api/middlewares/checking')
		.then(function(response) {
			setTimeout(function() {
				if(!response.data) {
					Router.push('/');
					Swal.fire('Kesalahan!', 'Anda belum terautentikasi!', 'warning');
				} else {
					Router.push(`/${response.data}`);
				}
			}, 2500);
		});
	},
	methods: {
	}
}
</script>